h2 {
  margin: .5em 0 .5em 0;
}

h4 {
  margin: 0 4em 0 0
}

.details-page {
  color: white;
}

.trailer-location{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.backdrop-image {
  height: 70vh;
  width: 100vw;
  position: relative;
}

.fade {
  display: inline-block;
  position: relative;
}

.fade:after {
  content: '';
  position: absolute;
  top: 0; right: 0;
  bottom: 0; left: 0;
  background: linear-gradient(to top, rgba(10,10,50,1) 0%,rgba(16,21,85,0) 69%,rgba(18,26,100,0) 100%);
}

.x-icon,
.play-button {
  cursor: pointer;
  height: 6em;
}

.play-button:hover {
  transition: .5s ease-in-out;
  transform: scale(1.2);
}

.x-icon:hover {
  transition: .5s ease-in-out;
  transform: scale(1.2);
}

.overlay {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  top: 42vh;
  left: 5vh;
  bottom: 0;
  right: 0;
  max-height: 30vh;
}

.left-side-overlay {
  display: flex;
  flex-direction: column;
  margin-left: 3em;
  max-height: 30vh;
}

.movie-details {
  display: flex;
  flex-direction: row;
  margin-top: 1em;
  flex-wrap: wrap;
}

.movie-title,
.rating {
  font-size: 3em;
  margin: 0;
}

.movie-title {
  font-size: 3.5em;
}

.trailer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.overview-section {
  display: flex;
  flex-direction: column;
  margin: 1em 3.6em 0 3.6em; 
  font-size: 1.2em;
}

.bottom-details {
  display: flex;
  flex-direction: row;
  margin-bottom: 3em;
}

.rating {
  font-size: 6em;
  margin: 0.5em 0.5em 0 0;
}

.play-button {
  margin-top: 1em;
}

.x-location {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  z-index: 1;
  top: 3%;
  left: 90%;
}

.loading {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height:80vh;
}

.loading-circle {
  width: 40vh;
  height: 40vh;
  animation-name: rotate;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  border-radius: 50%;
}

.sad-cloud {
  height: 30vh
}

.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 8em;
}

@keyframes rotate{
  from{ transform: rotate(-360deg); }
  to{ transform: rotate(360deg); }
}

@media screen and (max-width: 375px) {
  .backdrop-image {
    width: 120vw;
    height: 50vh;
  }
  
  .details-page{
    overflow-x: hidden;
  }

  .rating {
    font-size: 50px;
    margin-right: 12px;
  }
  .overlay {
    width: 100vw;
    font-size: x-small;
    top: -10vh;
    left: 0;
    position: relative;
  }

  .bottom-details {
    font-size: medium;
    margin: 0 2em 0 0
  }

  .left-side-overlay {
    margin-left: 20px;
  }

  .overview-section{
    margin-left: 20px;
    width: 100vw;
    margin-right: 30px;
    margin-top: -3vh;
  }
  .x-icon {
    position: relative;
    margin: 0;
    height: 60px;
  }
  .x-location {
    display: inline;
    margin-top: -14px;
    margin-left: 83vw;
    left: 0;
  }

  p {
    margin-right: 21px;
  }

  .play-button {
    height: 11vh;
  }

  h2 {
    font-size: 23px;
  }

  iframe {
    width: 300px;
    height: 200px;
  }
  
}